				"html": "<h3>Getting Started</h3>\n<p>Before we begin, you may want to <a href=\"http://clojure.org/guides/getting_started\">install Clojure</a> and <a href=\"http://leiningen.org/#install\">Leiningen</a> (which is a tool for managing projects in Clojure). This will let you run Clojure in the command line using a REPL (Read-Evaluate-Print-Loop).</p>\n<h2>Defining Variables</h2>\n<p>The bread and butter of any programming language are variables and functions. Let's define a variable!</p>\n<pre><code>(def our-string \"Hello world!\")\n</code></pre>\n<p>Easy peasy. That piece of code uses the <code>def</code> macro to associate a string (<code>\"Hello world!\"</code>) to a symbol (<code>our-string</code>). We could also have defined a number, such as <code>1</code> or <code>1.1</code>, a character, such as <code>\\a</code> or <code>\\Z</code>, or something more complicated like a list or a regular expression (<em>uuuugh</em>).</p>\n<p>Note that our code is inside parentheses, like a list, because everything in a Lisp is a list! (That will be very important when we start talking about macros.)</p>\n<h2>Defining Functions</h2>\n<p>Now, let's define a function!</p>\n<pre><code>(defn hello-world [] (println our-string))\n</code></pre>\n<p>This is a bit more complex. Like <code>def</code>, it uses a macro (<code>defn</code>) to create a variable - although this time, that variable is a function. The empty vector (a vector is a type of list -- think of it like an array) after <code>hello-world</code> defines the arguments to that function -- in this case, we don't have any. The code after that is what the function does. It evaluates <code>our-string</code>, which is equal to <code>\"Hello world!\"</code>, and prints it to the console. Let's run it!</p>\n<pre><code>(hello-world)\n; => Hello world!\n;    nil\n</code></pre>\n<p>You could also write this:</p>\n<pre><code>(def hello-world (fn [] (println our-string)))\n</code></pre>\n<p><code>defn</code> is just a shortcut to help keep your code concise. <code>(defn ...)</code> and <code>(def ... (fn ...))</code> are the same in practice.</p>\n<h2>Parameters</h2>\n<p>Well, that was nice, but it wasn't really exciting. Let's try a function with parameters. How about one that adds 3 numbers?</p>\n<pre><code>(defn add [x y z] (+ x y z))\n(add 1 2 3)\n; => 6\n</code></pre>\n<p>...Hold on. <code>(+ x y z)</code>? That looks funny. Well, Lisps are written using \"prefix notation\", which means that the function always comes first. Since all mathematical operators in Lisp (<code>+ - * /</code>) are just functions, they also come before their arguments (in this case, <code>x y z</code>).</p>\n<p>You'll notice that our vector has some stuff in it now: <code>[x y z]</code>! Whenever a function has parameters, you define them in that vector next to the function's name.</p>\n<h3>Destructuring</h3>\n<p>A great feature about arguments in Clojure is destructuring. It allows you to 'pull' elements out of a list.</p>\n<pre><code>(defn add [[x y] z] (+ x y z))\n(add [1 2] 3)\n; => 6\n</code></pre>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=2\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://ideone.com/SWlvKn\">IDEOne it!</a></p>\n<p>The arguments to that function are a collection (<code>[x y]</code>) and a number (<code>z</code>). We can use destructuring to pull the first and second elements out of the list, and call them <code>x</code> and <code>y</code>.</p>\n<h3>Functions with any number of parameters</h3>\n<p>You can also define a function with an arbitrary number of arguments using <code>&#x26;</code>.</p>\n<pre><code>(defn demonstrate-rest [first &#x26; rest]\n  (println first)\n  (println rest))\n(demonstrate-rest 1 \"foo\" [\"bar\" 22])\n; => 1\n;    (\"foo\" [\"bar\" 22])\n;    nil\n</code></pre>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=2\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://ideone.com/VftymP\">IDEOne it!</a></p>\n<p>As you can see, using <code>&#x26;</code> separated our function's arguments into one variable called <code>first</code> and a list of variables called <code>rest</code>. This means that our function could have any number of arguments!</p>\n<h2>Returning</h2>\n<p>You may have noticed some odd things. Whenever we use <code>println</code>, it seems like <code>nil</code> is showing up in our output. Furthermore, our <code>add</code> function returns <code>6</code>, but we never told it to return anything.</p>\n<p>In Clojure, returns are 'implicit'. If you've used Ruby, you're probably familiar with this concept. Rather than telling our function to return something, it evaluates all the code inside its body, and returns the result. Our <code>add</code> function, for example, evaluates <code>(+ x y z)</code>, and then returns that result.</p>\n<p>The reason why our functions that use <code>println</code> output <code>nil</code> is because <code>println</code> evaluates to <code>nil</code>. (<code>nil</code> is like <code>null</code> or <code>None</code> -- it represents nothing.)</p>\n<p>| <a href=\"//forum.freecodecamp.com/t/what-is-clojure/18419\"><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/point_left.png?v=2\" alt=\":point_left:\" title=\":point_left:\"> Previous</a> | <a href=\"//forum.freecodecamp.com/t/clojure-resources/18422\"><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/book.png?v=2\" alt=\":book:\" title=\":book:\"> Home <img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/book.png?v=2\" alt=\":book:\" title=\":book:\"></a> | <a href=\"//forum.freecodecamp.com/t/clojure-conditionals/18412\">Next <img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/point_right.png?v=2\" alt=\":point_right:\" title=\":point_right:\"></a>|<br>\n| <a href=\"//forum.freecodecamp.com/t/what-is-clojure/18419\">Summary</a> | <a href=\"//forum.freecodecamp.com/t/clojure-resources/18422\">Table of Contents</a> | <a href=\"//forum.freecodecamp.com/t/clojure-conditionals/18412\">Conditionals</a>|</p>",
